import React, { Component } from 'react';
import Typed from 'react-typed';

class Header extends Component {
  render() {

    if(this.props.data){
      var name = this.props.data.name;
      var occupation= this.props.data.occupation;
      var description= this.props.data.description;
      var city= this.props.data.address.city;
      var networks= this.props.data.social.map(function(network){
        return <li key={network.name}><a href={network.url} target="_blank"><i className={network.className}></i></a></li>
      })
      var options = [this.props.data.description] 
      
    }
    

    return (
      <header id="home">

      <nav id="nav-wrap">

         <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
	      <a className="mobile-btn" href="#home" title="Hide navigation">Hide navigation</a>

         <ul id="nav" className="nav">
            <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
            <li><a className="smoothscroll" href="#about">About</a></li>
	          <li><a className="smoothscroll" href="#experience">Experience</a></li>
            <li><a className="smoothscroll" href="#skills">Skills</a></li>
            <li><a className="smoothscroll" href="#portfolio">Works</a></li>
            <li><a className="smoothscroll" href="#testimonials">Recommendations</a></li>
            <li><a className="smoothscroll" href="#contact">Contact</a></li>
         </ul>

      </nav>

      <div className="row banner">
         <div className="banner-text">
            <h1 id="heading" className="responsive-headline">I'm {name}.</h1>
            <h3>Currently I'm working at Traform as {occupation}.
            <p id="heading-description"> 
              <Typed strings={['I specialize in Full Stack development using JAVA, JavaScript, React, Redux , Node, Spring Boot, Hibernate, AngularJS. I always look for optimizing the code and enhancing the performance of the application. I am self motivated, good team player and a quick learner. If you are looking for a tech geek who is goal oriented, willing to learn new technologies, and who likes to get things done in time, then I am the man for the job.']} typeSpeed={30}  /></p>.</h3>
            <hr />
            <ul className="social">
               {networks}
            </ul>
         </div>
      </div>

      <p className="scrolldown">
         <a className="smoothscroll" href="#about"><i className="icon-down-circle"></i></a>
      </p>

   </header>
    );
  }
}

export default Header;



